@tailwind base;
@tailwind components;
@tailwind utilities;
.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px; }

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #fff;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #B9B9B9 transparent transparent transparent; }

.lds-ring div:nth-child(1) {
  animation-delay: -0.45s; }

.lds-ring div:nth-child(2) {
  animation-delay: -0.3s; }

.lds-ring div:nth-child(3) {
  animation-delay: -0.15s; }

@keyframes lds-ring {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

body {
  font-family: 'Noto Sans TC', 'sans-serif';
  font-size: 16px; }

.material-icons.text-4xl {
  font-size: 2rem; }

@media lg {
  .material-icons.lg\:hidden {
    @apply hidden; } }

.recommendation-wall::-webkit-scrollbar {
  display: none;
  scrollbar-width: none;
  -ms-overflow-style: none; }

.recommendation-card {
  min-width: 350px; }

.recommendation-card:not(:last-child) {
  margin-right: 30px; }

header a[data-scroll="true"] {
  @apply text-primary; }

[data-topMenu-burger="show"] {
  @apply text-primary; }

[data-topMenu-burger="close"] {
  @apply text-dark; }

[data-loading="true"] {
  @apply flex; }

[data-loading="false"] {
  @apply hidden; }

[data-cartMsg="true"] {
  @apply table; }

[data-cartMsg="false"] {
  @apply hidden; }

[data-popUp="true"] {
  @apply flex; }

[data-popUp="false"] {
  @apply hidden; }
